// React
import { useState, useEffect } from 'react';
// Assets
import './e-panel.css';
// Library
import {
  verifyDomainStatus,
  verifyAPIStatus,
  verifyClientList,
  verifyKeyStatus
} from './monitors/domainMonitor';
// Shared Library
import { POST } from '../../shared/library/server/api';


const APISettings = (props:any) => {
  const [ appNameSetting, setAppName ] = useState<string>(props.panel.name);
  const [ appAPISetting, setAppAPI ] = useState<string>(props.panel.api);
  const [ appKeySetting, setAppKey ] = useState<string>(props.panel.apiKey);
  const [ appClients, setAppClients ] = useState<any>(
    [ props.panel.primaryClient, ...props.panel.secondaryClients ]
  );

  const [ currentDomainStatus, setDomainStatus ] = useState<any>(undefined);
  const [ currentAPIStatus, setAPIStatus ] = useState<any>(undefined);
  const [ currentKeyStatus, setKeyStatus ] = useState<any>(undefined);
  const currentDate = new Date();

  useEffect(() => {
    if (props.panel.isWeb) verifyDomainStatus(
      `https://${appNameSetting}/`,
      (domainStatus:boolean) => {setDomainStatus(domainStatus)}
    );
    if (props.panel.api) verifyAPIStatus(
      `https://${appNameSetting}/${appAPISetting}/status`,
      (apiStatus:boolean) => {setAPIStatus(apiStatus)}
    );
    if (props.panel.apiKey) verifyKeyStatus(
      `https://${appNameSetting}/${appAPISetting}/verify?key=${props.panel.apiKey}`,
      (keyStatus:boolean) => {setKeyStatus(keyStatus)}
    );
    if (props.panel.primaryClient) verifyClientList(
      `https://${appNameSetting}/${appAPISetting}/clients?key=${props.panel.apiKey}`,
      (localClients:any) => {
        setAppClients([localClients.primaryClient, ...localClients.secondaryClients])
      }
    );
  }, [appNameSetting, appAPISetting, appKeySetting, props.panel]);

  const NoIcon = () => <></>
  const ReactIcon = () => <i
    className="fab fa-react"
    style={{ margin: 'auto 0 auto 0', color: 'rgba(0,0,0,.25)' }}
  ></i>
  const NativeIcon = () => <i
    className="fa fa-mobile-alt"
    style={{ margin: 'auto 0 auto 2px', color: 'rgba(0,0,0,.25)' }}
  ></i>
  const APIKeyIcon = () => <i
    className="fa fa-lock"
    style={{
      margin: 'auto 0 auto 2px',
      color: currentKeyStatus === undefined ? 'rgba(0,0,0,.25)' : currentKeyStatus ? 'rgb(25,255,25)' : 'rgb(255,25,25)'
    }}
  ></i>
  const CheckIcon = () => <i
    className="fa fa-check"
    style={{ margin: 'auto 0 auto 3px', color: 'rgb(25,255,25)' }}
  ></i>
  const CrossIcon = () => <i
    className="fa fa-times"
    style={{
      margin: 'auto 0 auto 4px',
      color: 'rgb(255,25,25)'
    }}
  ></i>

  const Setting = (props:any) => {
    const [ isEdit, setEdit ] = useState(false);
    const AdditionalIcon = props.additionalIcon;
    const isDropDown = props.data instanceof Array;
    const dataPrefix = props.prefix === undefined ? "" : props.prefix
    const dataPostfix = props.postfix === undefined ? "" : props.postfix

    return <div style={{ height: '98px' }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', margin: '0 2px' }}>
        <label>{props.label}</label>
        <i
          className={isEdit ? "fa fa-window-close" : "fa fa-edit"}
          style={{ cursor: 'pointer', color: isEdit ? "red" : "black" }}
          onClick={() => setEdit(!isEdit)}
        ></i>
      </div>
      <hr/>
      { isEdit ?
          <form className="flex" onSubmit={
            (event:any) => {
              event.preventDefault();
              const _inputEL = document.getElementById(props.label) as HTMLInputElement;
              if (props.eventHandler !== undefined) props.eventHandler(_inputEL);
              setEdit(false);
            }
          }>
            <p style={{ margin: 'unset', padding: 'unset', color: 'black' }}>
              {props.prefix}
              { isDropDown ?
                <select
                  id={props.label}
                  style={{
                    margin: '0 0',
                    padding: '2px',
                    border: 'unset',
                    borderRadius: '6px',
                    backgroundColor: 'rgba(255,255,255,0.5)'
                  }}
                  onChange={(event:any) => props.eventHandler(event.target)}
                >{
                  props.data.map((dataOption:string) => <option value={dataOption}>{dataOption}</option>)
                }</select>
                :
                <input
                  id={props.label}
                  type="text"
                  style={{
                    minWidth: props.width !== undefined ?
                      props.width
                    :
                      'fit-content !important',
                    margin: '0 0',
                    padding: '4px',
                    border: 'unset',
                    borderRadius: '6px',
                    backgroundColor: 'rgba(255,255,255,0.5)'
                  }}
                  placeholder={props.data}
                />
              }
              {props.postfix}
            </p>
          </form>
        :
          <div style={{ display: 'flex', justifyContent: 'space-between', marginRight: '2px' }}>
            <p style={{ color: 'black', userSelect: 'all', cursor: 'pointer', margin: 'unset' }}>
              {
                isDropDown ?
                  props.data[0]
                :
                props.hideText !== undefined && props.hideText === true ?
                  "******************************"
                :
                  dataPrefix + props.data + dataPostfix
              }
            </p>
            { props.additionalIcon !== undefined ? <AdditionalIcon/> : <></>}
          </div>
      }
    </div>
  }

  const ConfirmationToggle = (_props:any) => {
    return <div id={_props.id} style={{ display: _props.display !== undefined ? _props.display : "block" }}>
      <button onClick={() => _props.confirm()}>Confirm</button>
      <button onClick={() => _props.cancel()}>Cancel</button>
    </div>
  }

  return <>
    <h2 className="Panel-Option-Header">API Settings</h2>
    <div style={{
      display: 'flex', flexDirection: 'column', justifyContent: 'left', maxWidth: '720px', minHeight: '500px',
      margin: '0 auto 5% auto', border: '1px solid #363b46', backgroundColor: 'rgb(245, 245, 245)', padding: '5%'
    }}>

      <h3 style={{ marginBottom: '32px' }}>Client Configuration</h3>

      <h3 style={{ marginBottom: '32px' }}>Sever Configuration</h3>

      <Setting
        label="Application Domain"
        prefix={ props.panel.isWeb ? "https://" : "" }
        data={appNameSetting}
        postfix={ props.panel.isWeb ? "/" : "" }
        additionalIcon={ currentDomainStatus === undefined ?
          NoIcon
        :
          currentDomainStatus === true ? CheckIcon : CrossIcon
        }
        eventHandler={(event:any) => {
          POST(`update/name/${props.panel.id}`, event.value,
            (resp:any) => alert("Sorry, an error was encountered."),
            (resp:any) => {
              setAppName(event.value);
              props.panel.name = event.value;
              props.panel.updatePanel(props.panel);
            }
          )
        }}
      />

      <Setting
        label="Application API"
        data={appAPISetting}
        prefix={props.panel.isWeb ? `https://${appNameSetting}/` : ''}
        postfix="/"
        additionalIcon={currentAPIStatus === undefined ? NoIcon : currentAPIStatus ? CheckIcon : CrossIcon}
        eventHandler={(event:any) => {
          POST(`update/api/${props.panel.id}`, event.value,
            (resp:any) => alert("Sorry, an error was encountered."),
            (resp:any) => {
              setAppAPI(event.value);
              props.panel.api = event.value;
              props.panel.updatePanel(props.panel);
            }
          )
        }}
      />

      <Setting
        label="API Key"
        data={appKeySetting}
        width="90%"
        additionalIcon={APIKeyIcon}
        eventHandler={(event:any) => {
          POST(`update/key/${props.panel.id}`, event.value,
            (resp:any) => alert("Sorry, an error was encountered."),
            (resp:any) => {
              setAppKey(event.value);
              props.panel.apiKey = event.value;
              props.panel.updatePanel(props.panel);
            }
          )
        }}
        hideText={true}
      />

      <Setting
        label="Primary Client"
        data={appClients}
        width="100%"
        additionalIcon={props.panel.isNative ? NativeIcon : ReactIcon}
        eventHandler={(event:any) => {
          POST(`update/primaryClient/${props.panel.id}`, event.value,
            (resp:any) => alert("Sorry, an error was encountered."),
            (resp:any) => {
              const thisToggle = document.getElementById("clientsSettingConfirmation") as HTMLElement;
              thisToggle.style.display = "flex !important";
            }
          )
        }}
      />

      <ConfirmationToggle
        id="clientsSettingConfirmation"
        display="none"
        confirm={ () => {
          alert(`${appClients}`);
        }}
        cancel={ () => {
          alert("x");
        }}
      />

    </div>

    <h5 style={{ textAlign: 'center' }}>
      © Easter Company {currentDate.getFullYear()}
    </h5>

  </>
}


export default APISettings;
