// React
import { useState, useEffect } from 'react';
// Library
import { xapi } from '../../../shared/library/server/api';
// Components
import Table from '../../../shared/components/tables/table';


const APIPosts = (props:any) => {
  /*  STATE MANAGEMENT  */
  const [ tableType, setTable ] = useState('post');
  const [ head, setHead ] = useState([]);
  const [ body, setBody ] = useState([]);


  /*  EFFECT CONTROLLER  */
  useEffect(() => {xapi(
    `https://${props.panel.name}/${props.panel.api}/${tableType}/list`,
    null, null,
    (resp:any) => { setHead(resp.head); setBody(resp.body); }
  )}, [props.panel, tableType])


  /*  NEW POST VIEWS  */
  const CreateNewRecord = () => {

    if (tableType === 'recipe') {
      return <div className="ep-create-record-view">
        <p>Name: </p><input type="text" id="recipe-name"/>
        <p>Categories: </p><input type="text" id="recipe-categories"/>
        <p>Image: </p><input type="text" id="recipe-image"/>
        <p>Description: </p><input type="text" id="recipe-description"/>
        <p>Prep Time: </p><input type="text" id="recipe-prep-time"/>
        <p>Cook Time: </p><input type="text" id="recipe-cook-time"/>
        <p>Ingredients: </p><input type="text" id="recipe-ingredients"/>
        <p>Utensils: </p><input type="text" id="recipe-utensils"/>
        <p>Approvals: </p><p>0</p>
      </div>
    }

    else if (tableType === 'recipe/ingredients') {
      return <div className="ep-create-record-view">
        <p>Name: </p><input type="text" id="ingredient-name"/>
        <p>Aliases: </p><input type="text" id="ingredient-aliases"/>
        <p>Icon: </p><input type="text" id="ingredient-icon"/>
        <p>Image: </p><input type="text" id="ingredient-image"/>
        <p>Description: </p><input type="text" id="ingredient-description"/>
        <p>Types: </p><input type="text" id="ingredient-types"/>
      </div>
    }

    else if (tableType === 'recipe/utensils') {
      return <div className="ep-create-record-view">
        <p>Name: </p><input type="text" id="utensil-name"/>
        <p>Aliases: </p><input type="text" id="utensil-aliases"/>
        <p>Icon: </p><input type="text" id="utensil-icon"/>
      </div>
    }

  }


  /*  PANEL POSTS VIEW  */
  return <div style={{ marginBottom: '5vh' }}>
    <div style={{ display: 'flex' }}>
      <h2 className="Panel-Option-Header" style={{
        textAlign: 'left', width: 'fit-content', margin: 'auto auto auto 64px'
      }}>Posts</h2>
      <div style={{ margin: '48px 0' }}>
        <select
          style={{ margin: 'auto 24px auto 0' }}
          value={tableType}
          onChange={(event:any) => setTable(event.target.value)}
        >
          <option value="post">Generic</option>
          <option value="job">Job</option>
          <option value="recipe">Recipe</option>
          <option value="recipe/ingredients">Ingredients</option>
          <option value="recipe/utensils">Utensils</option>
        </select>
      </div>
    </div>
    <div id="Panel-Content-Container" style={{ flexDirection: 'column', textAlign: 'center' }}>
      <Table
        /* Config */
        name={tableType}
        previewSize={10}
        SVGFill="#282c34"
        /* Table Content */
        headers={head}
        data={body}
        /* API Paths */
        view={true}
        create={CreateNewRecord}
        delete={`${tableType}/delete`}
        /* Table Options */
        onCreate={() => {
          if (tableType === 'recipe') {
            return null
          }
          else if (tableType === 'recipe/ingredients') {
            return null
          }
          else if (tableType === 'recipe/utensils') {
            const data = {
              name: (document.getElementById("utensil-name") as HTMLInputElement).value,
              aliases: (document.getElementById("utensil-aliases") as HTMLInputElement).value,
              icon: (document.getElementById("utensil-icon") as HTMLInputElement).value
            }
            xapi(
              `https://${props.panel.name}/${props.panel.api}/${tableType}/list`,
              null, null,
              () => {}
            )
          }
        }}
      />
    </div>
  </div>
}


export default APIPosts;
